import React, { useEffect, useState, useRef } from 'react';
import { useForm } from 'react-hook-form';
import { UserIdValid, signup } from '../../api/signUp';
import { imgUpload } from '../../api/imgUpload';
import { BASE_URL } from '../../api/baseUrl';
import DefaultProfileInput from '../../images/basic-profile-img.svg';
import { useLocation, useNavigate } from 'react-router-dom';
//import Input from '../../components/common/Input/Input';
import {
  StyledStartButton,
  ImageFormContainer,
  ProfileFormContainer,
  InputImage,
  StyledError,
  StyledLabel,
  StyledInput,
  ProfileInputImgButton,
  StyledProfileImg,
  StyledSaveButton,
} from './ProfileSettingStyle';

// ID = fz_stitch

const ProfileSettingForm = () => {
  const {
    register,
    handleSubmit,
    clearErrors,
    setError,
    setValue,
    formState: { errors, isValid },
  } = useForm({
    mode: 'onChange',
    defaultValue: {
      username: null,
      userid: null,
      userintro: null,
    },
  });

  const [profileImg, setProfileImg] = useState(null);
  const [abledBtn, setAbledBtn] = useState(true);
  const inputRef = useRef(null);
  const navigate = useNavigate();
  const location = useLocation();
  const data = location.state;

  useEffect(() => {
    if (location.pathname === '/signup/profile') {
      setValue('image', DefaultProfileInput);
      setValue('username', null);
      setValue('userid', null);
      setValue('userintro', null);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [location.pathname]);

  const checkUserIdValid = async (userid) => {
    try {
      const res = await UserIdValid(userid);
      const reqMsg = res.data.message;
      clearErrors('userid');
      if (reqMsg === '  ID .') {
        setError('userid', {
          type: 'manual',
          message: '*   ID :(',
        });
        return false;
      } else {
        clearErrors('userid');
        return true;
      }
    } catch (errors) {
      return false;
    }
  };

  const handleImageChange = async (event) => {
    const formData = new FormData();
    const file = event.target.files[0];
    formData.append('image', file);
    await imgUpload(formData).then((res) => {
      const imgUrl = `${BASE_URL}/` + res.data.filename;
      setProfileImg(imgUrl);
    });
  };

  const handleSubmitData = async (formData) => {
    try {
      const isValidUserId = await checkUserIdValid(formData.userid);
      if (isValidUserId) {
        await signup(formData, data, profileImg).then(
          navigate('/signup/signupsuccess', {
            state: {
              userid: formData.userid,
              username: formData.username,
              userintro: formData.userintro,
            },
          }),
        );
      }
    } catch (errors) {
      console.error(errors);
    }
  };

  //isValid setAbledBtn   
  useEffect(() => {
    setAbledBtn(isValid);
  }, [isValid, setAbledBtn]);

  return (
    <ProfileFormContainer onSubmit={handleSubmit(handleSubmitData)}>
      <ImageFormContainer>
        <label>
          <InputImage
            id='profileImg'
            type='file'
            accept='image/jpg, image/jpeg, image/png'
            ref={inputRef}
            onChange={handleImageChange}
          />
        </label>
        <ProfileInputImgButton
          type='button'
          onClick={() => inputRef.current.click()}
        >
          <StyledProfileImg
            src={profileImg || DefaultProfileInput}
            alt=' '
          />
        </ProfileInputImgButton>
      </ImageFormContainer>
      <StyledLabel> </StyledLabel>
      <StyledInput
        id='username'
        type='text'
        autoComplete='off'
        {...register('username', {
          required: '  ',
          minLength: {
            value: 2,
            message: '   2  .',
          },
          maxLength: {
            value: 10,
            message: '   10 .',
          },
        })}
        placeholder='2~10   .'
      />
      {errors.username && <StyledError>{errors.username?.message}</StyledError>}
      <StyledLabel> ID</StyledLabel>
      <StyledInput
        id='userid'
        type='text'
        autoComplete='off'
        {...register('userid', {
          required: 'ID  ',
          pattern: {
            value: /^[0-9a-zA-Z._]+$/,
            message: '*, ,        :(',
          },
        })}
        placeholder=', , (.),(_)  .'
      />
      {errors.userid && <StyledError>{errors.userid?.message}</StyledError>}
      <StyledLabel></StyledLabel>
      <StyledInput
        id='userintro'
        type='text'
        autoComplete='off'
        {...register('userintro', {
          required: '  !',
        })}
        placeholder='     .'
      />
      {errors.userintro && (
        <StyledError>{errors.userintro?.message}</StyledError>
      )}

      {location.pathname === '/signup/profile' ? (
        <StyledStartButton
          className='btn-signup'
          $bgcolor={abledBtn ? 'active' : 'inactive'}
          disabled={!abledBtn}
        >
          
        </StyledStartButton>
      ) : (
        <StyledSaveButton
          className='btn-profile-edit'
          $bgcolor={abledBtn ? 'active' : 'inactive'}
          disabled={!abledBtn}
        >
          
        </StyledSaveButton>
      )}

      {/*   <Input
          label=' '
          id='username'
          type='text'
          placeholder='2~10   .'
          onChange={handleFieldChange}
          hasError={hasError}
          registerOptions={{
            ...register('username', {
              requried: '  ',
            }),
          }}
        />
        <ErrorStyle>{errors.userName?.message}</ErrorStyle>
        <Input
          label=' ID'
          id='user-id'
          type='text'
          placeholder=', , (.),(_)  .'
          hasError='true'
        />
        <ErrorStyle>{errors.userID?.message}</ErrorStyle>
        <Input
          label=''
          id='user-desc'
          type='text'
          placeholder='     !'
          h */}
    </ProfileFormContainer>
  );
};

export default ProfileSettingForm;
