import React, { useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useNavigate } from 'react-router-dom';
import { EmailValid } from '../../api/signUp';
//import Input from '../common/Input/Input';
import {
  StyledSignUpWrap,
  StyledTitle,
  StyledFormWrap,
  StyledButton,
  StyledLabel,
  StyledInput,
  StyledError,
} from './SignUpStyle';

const SignUpForm = () => {
  const {
    register,
    handleSubmit,
    clearErrors,
    setError,
    formState: { errors, isValid },
  } = useForm({
    mode: 'onSubmit',
    defaultValues: {
      email: null,
      password: null,
    },
  });

  const navigate = useNavigate();
  const [abledBtn, setAbledBtn] = useState(true);

  //isValid setAbledBtn   

  const checkEmailValid = async (email) => {
    try {
      const res = await EmailValid(email);
      const reqMsg = res.data.message;
      clearErrors('email');
      if (reqMsg === '    .') {
        setError('email', {
          type: 'manual',
          message: '    .',
        });
        return false;
      } else {
        clearErrors('email');
        return true;
      }
    } catch (error) {
      return false;
    }
  };

  const handleSubmitData = async (data) => {
    const isValidEmail = await checkEmailValid(data.email);
    if (isValidEmail) {
      navigate('./profile', {
        state: {
          email: data.email,
          password: data.password,
        },
      });
    }
  };

  useEffect(() => {
    setAbledBtn(isValid);
  }, [isValid, setAbledBtn]);

  return (
    <StyledSignUpWrap>
      <StyledTitle> !</StyledTitle>
      <StyledFormWrap onSubmit={handleSubmit(handleSubmitData)}>
        {/* <Input
          label=''
          id='email'
          type='email'
          placeholder=' .'
          hasError={hasError}
          registerOptions={{
            ...register('email', {
              required: '  .',
              pattern: {
                value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                message: '    :(',
              },
            }),
          }}
        />
        {errors.email && <ErrorStyle>{errors.email?.message}</ErrorStyle>}

        <Input
          label=''
          type='passwordID'
          placeholder=' '
          hasError={hasError}
          registerOptions={{
            ...register('password', {
              required: '  .',
              pattern: {
                value: 6,
                message: ' 6  :(',
              },
            }),
          }}
        />
        <ErrorStyle>{errors.password?.message}</ErrorStyle> */}
        <StyledLabel></StyledLabel>
        <StyledInput
          autoComplete='off'
          {...register('email', {
            required: '  ',
            //api     
            pattern: {
              value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
              message: '    :(',
            },
          })}
          placeholder=' '
        />
        {errors.email && <StyledError>{errors.email?.message}</StyledError>}
        <StyledLabel></StyledLabel>
        <StyledInput
          type='password'
          {...register('password', {
            required: '  .',
            minLength: {
              value: 6,
              message: ' 6  :(',
            },
          })}
          placeholder=' '
        />
        <StyledError>{errors.password?.message}</StyledError>
        <StyledButton
          type='submit'
          className='btn-signup'
          $bgcolor={abledBtn ? 'active' : 'inactive'}
          disabled={!abledBtn}
        >
          
        </StyledButton>
      </StyledFormWrap>
    </StyledSignUpWrap>
  );
};

export default SignUpForm;
